import React, { useMemo } from 'react'
import { isPermission } from '../../../utils/common'

const LeaveBanner = (props) => {
    const { handleActiveTab } = props
    return (
        <>
            <div className="cardBox leaveAppli h-100">
                <div className="row">
                    <div className="col-4 selectModal position-relative z-1">
                        <div className="row px-md-1 ">
                           {isPermission("leave_type") && <div className="col-md-6 px-md-2 mb-3" onClick={() => handleActiveTab("leave")}>
                                <span
                                    className="totalBx h-100"
                                >
                                    <figure>
                                        <img src="images/applyleavesIcon.svg" alt="" width={50} />
                                    </figure>
                                    <h3 className="fw-bolder my-0">Leave</h3>
                                    <img
                                        src="images/firstlineBlue.svg"
                                        className="cornerLine"
                                        alt=""
                                        width={95}
                                    />
                                </span>
                            </div>}
                           {isPermission("leave_setting") && <div className="col-md-6 px-md-2 mb-3" onClick={() => handleActiveTab("leave_setting")}>
                                <span
                                    className="totalBx h-100"
                                >
                                    <figure>
                                        <img src="images/attendancelogIcon.svg" alt="" width={50} />
                                    </figure>
                                    <h3 className="fw-bolder my-0">Leave Setting</h3>
                                    <img
                                        src="images/secondlineBlue.svg"
                                        className="cornerLine"
                                        alt=""
                                        width={80}
                                    />
                                </span>
                            </div>}

                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default LeaveBanner
