import React, { useEffect, useState } from 'react'
import { useCustomTranslation } from '../../../translation'
import {  Label, Textarea } from '../../commanComponent'
import CustomSelect from '../../commanComponent/CustomSelect'
import { CustomButton, CustomError } from '../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import { leaveAction } from '../../../core/resource/actions'
import { el } from 'date-fns/locale'
import { tuple } from 'yup'
const EncashmentFormula = (props) => {
    const dispatch = useDispatch()
    const { toggleCreateFormula, allLeaveConstantData, setCreateUpdateLeaveFormData } = props
    const {
        checkLeaveEncashmentFormulaPending,
        checkLeaveEncashmentFormulaRes,
        checkLeaveEncashmentFormulaError
    } = useSelector(state => state.leave)
    const { t } = useCustomTranslation()
    const [encashmentFormula, setEncashmentFormula] = useState({
        brackets: '',
        abbreviation: '',
        operators: ''
    })
    const [validDescription, setValidDescription] = useState('')
    const [isFillDesc, setIsFillDesc] = useState(false)

    const handleFromChange = (e, fieldName) => {
        const name = fieldName
        const value = e;

        setEncashmentFormula(prevState => {
            const updatedState = {
                ...prevState,
                [name]: value,
            };
            return updatedState;
        });
        setIsFillDesc(false)
    };
    const onAddFormulaField = () => {
        const { brackets, abbreviation, operators } = encashmentFormula
        let addFormulaFields =
            (brackets?.value || '') +
            (abbreviation?.value || '') +
            (operators?.value || '');

        setValidDescription((prev) => prev + addFormulaFields);
        setEncashmentFormula({
            brackets: '',
            abbreviation: '',
            operators: ''
        })
    }

    const checkEncashmentFormula = () => {
        if (validDescription) {
            setIsFillDesc(false)
            const payload = {
                formula: validDescription
            }
            dispatch(leaveAction.checkLeaveEncashmentFormulaRequest(payload))
        } else {
            setIsFillDesc(true)
        }
    }
    const saveEncashmentFormula = () => {
        setCreateUpdateLeaveFormData((pre) => ({
            ...pre,
            ["encashable_formula"]: validDescription
        }))
    }


    useEffect(() => {
        if (!checkLeaveEncashmentFormulaPending) {
            if (checkLeaveEncashmentFormulaRes) {
                dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_CHECK_LEAVE_ENCASHMENT_FORMULA_STORE"))
                return;
            }
            if (checkLeaveEncashmentFormulaError) {
                if (checkLeaveEncashmentFormulaError?.code === 200) {
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_CHECK_LEAVE_ENCASHMENT_FORMULA_STORE"))
                }
            }
        }
    }, [
        checkLeaveEncashmentFormulaError,
        checkLeaveEncashmentFormulaRes,
        checkLeaveEncashmentFormulaPending
    ]);


    return (
        <div className='border p-3' style={{ backgroundColor: "#DCDCDC" }}>
            <div className='p-3' style={{ backgroundColor: "white" }}>
                <div className="header ps-2 d-flex justify-content-between">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{t("headings.encashmentFormula")}</h5>
                    <button type="button" className="btn-close" onClick={() => toggleCreateFormula()}></button>
                </div>
                <hr className='m-1' />
                <div className="body mt-2">
                    <div className="formPanel">
                        <div className='row'>
                            <div className="col-md-3 form-group mb-3">
                                <Label text={t("labels.brackets")}
                                />
                                <CustomSelect
                                    data={allLeaveConstantData && allLeaveConstantData?.brackets}
                                    placeholder={t("placeholders.select")}
                                    value={encashmentFormula?.brackets || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "brackets")}
                                    name="brackets"
                                />
                            </div>
                            <div className="col-md-3 form-group mb-3">
                                <Label text={t("labels.abbreviation")}
                                />
                                <CustomSelect
                                    data={allLeaveConstantData && allLeaveConstantData?.abbreviations}
                                    placeholder={t("placeholders.select")}
                                    value={encashmentFormula?.abbreviation || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "abbreviation")}
                                    name="abbreviation"
                                />
                            </div>
                            <div className="col-md-3 form-group mb-3">
                                <Label text={t("labels.operators")}
                                />
                                <CustomSelect
                                    data={allLeaveConstantData && allLeaveConstantData?.operators}
                                    placeholder={t("placeholders.select")}
                                    value={encashmentFormula?.operators || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "operators")}
                                    name="operators"
                                />
                            </div>
                            <div className="col-md-3 form-group mb-3">
                                <CustomButton
                                    text={t("btns.add")}
                                    classNames={"btn btn-primary gradientBg mt-4"}
                                    handleClick={() => onAddFormulaField()}
                                />
                            </div>
                        </div>
                        <div className='row'>
                            <div className="col-md-9 form-group mb-3">
                                <Textarea
                                    value={validDescription}
                                    placeholder={t("placeholders.leaveExpression")}
                                    onChange={(e) => setValidDescription(e.target.value)}
                                />
                                {isFillDesc && (
                                    <CustomError text={"Place add formula to validate"} />
                                )}
                            </div>
                        </div>
                        <div className='row'>
                            <div className="col-md-9  mb-3 d-flex justify-content-end" >
                                <CustomButton
                                    text={t("btns.validate")}
                                    classNames={"btn btn-success gradientBg "}
                                    handleClick={() => checkEncashmentFormula()}
                                    isLoading={checkLeaveEncashmentFormulaPending}
                                />
                            </div>
                        </div>

                    </div>
                </div>
                <div className="footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={t("btns.save")}
                            classNames={"btn btn-success gradientBg mt-2"}
                            handleClick={() => saveEncashmentFormula()}
                        />
                    </div>
                </div>
            </div>
        </div>

    )
}

export default EncashmentFormula
