import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { createShortName, defaultMultiSelectOptions, getDataWithLabelValue } from '../../../utils/common'
import { validateSchema } from '../../../utils/ValidationHelper'
import { addLeaveSchema } from '../../../utils/ValidationSchema'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import { CustomButton, CustomError } from '../../globalComponents'
import CustomSelect from '../../commanComponent/CustomSelect'
import { useCustomTranslation } from '../../../translation'
import EncashmentFormula from './EncashmentFormula'
import { conforms } from 'lodash'


const CreateUpdateLeave = (props) => {
    const { t } = useCustomTranslation()
    const {
        handleOpenClose,
        openModel,
        handleCreateUpdateLeave,
        allLeaveConstantData,
        selectedLeaveData,
        appConstantData,
        allEmployeeList
    } = props
    const [errors, setErrors] = useState('')
    const {
        addLeaveTypePending, updateLeaveTypePending
    } = useSelector(state => state.leave);
    const [activeTab, setActiveTab] = useState('basic')
    const [isOpenFormula, setIsOpenFormula] = useState(false)
    const [createUpdateLeaveFormData, setCreateUpdateLeaveFormData] = useState({
        name: '',
        short_name: '',
        max_leave_per_year: '',
        carry_forward_limit: '',
        expire_on: null,
        leave_unit: null,
        leave_type: null,
        is_carry_forwardable: false,
        is_active: true,
        can_view_on_probation: true,
        is_credit_on_probation: false,
        is_encashment: false,
        is_expirable: false,
        is_deduction: true,
        is_allowed_on_notice: false,
        balance_based_on: null,
        validity_start_date: '',
        validity_end_date: '',
        effective_after_number: '',
        effective_after_name: '',
        effective_after_value: '',
        minimum_days_for_request: '',
        maximum_days_for_request: '',
        minimum_interval_between_request: '',
        applicable_gender: '',
        applicable_marital_status: '',
        applicable_employee: '',
        is_applicable_for_esi_employee: false,
        minimum_service_days_to_apply: '',
        max_days_encashable: '',
        encashable_formula: ''
    })
    useEffect(() => {
        if (selectedLeaveData) {
            const maritalData = getDataWithLabelValue(selectedLeaveData.applicable_marital_status)
            const applicableGenderData = getDataWithLabelValue(selectedLeaveData.applicable_gender)
            const applicableEmployee =defaultMultiSelectOptions(allEmployeeList,selectedLeaveData?.applicable_employee)
            setCreateUpdateLeaveFormData({
                name: selectedLeaveData.name,
                short_name: selectedLeaveData.short_name,
                is_carry_forwardable: selectedLeaveData.is_carry_forwardable,
                is_active: selectedLeaveData.is_active,
                can_view_on_probation: selectedLeaveData.can_view_on_probation,
                is_credit_on_probation: selectedLeaveData.is_credit_on_probation,
                is_encashment: selectedLeaveData.is_encashment,
                is_expirable: selectedLeaveData.is_expirable,
                is_deduction: selectedLeaveData.is_deduction,
                validity_start_date: selectedLeaveData.validity_start_date,
                leave_type: { value: selectedLeaveData.leave_type, label: selectedLeaveData.leave_type_name },
                validity_end_date: selectedLeaveData.validity_end_date,
                effective_after_number: selectedLeaveData.effective_after_number,
                effective_after_name: { value: selectedLeaveData.effective_after_name, label: selectedLeaveData?.effective_after_name_name },
                effective_after_value: { value: selectedLeaveData.effective_after_value, label: selectedLeaveData.effective_after_value_name },
                minimum_days_for_request: selectedLeaveData.minimum_days_for_request,
                maximum_days_for_request: selectedLeaveData.maximum_days_for_request,
                minimum_interval_between_request: selectedLeaveData.minimum_interval_between_request,
                applicable_gender: applicableGenderData,
                applicable_marital_status: maritalData,
                applicable_employee: applicableEmployee,
                balance_based_on: { label: selectedLeaveData?.balance_based_on_name, value: selectedLeaveData?.balance_based_on },
                expire_on: { value: selectedLeaveData?.expire_on, label: selectedLeaveData?.expire_on_name },
                leave_unit: { value: selectedLeaveData?.leave_unit, label: selectedLeaveData?.leave_unit_name },
                max_leave_per_year: selectedLeaveData?.max_leave_per_year,
                carry_forward_limit: selectedLeaveData?.carry_forward_limit,
                is_applicable_for_esi_employee: selectedLeaveData?.is_applicable_for_esi_employee,
                minimum_service_days_to_apply: selectedLeaveData?.minimum_service_days_to_apply || '',
                max_days_encashable: selectedLeaveData?.max_days_encashable,
                encashable_formula: selectedLeaveData?.encashable_formula,

            })
        }
    }, [selectedLeaveData])
    const handleFromChange = (e, fieldName) => {
        // For custom selects, use the value passed directly as `e`
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setCreateUpdateLeaveFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            if (name === 'name') {
                updatedState.short_name = createShortName(value);
            }
            return updatedState;
        });
        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };
    const submitCreateUpdateLeave = async () => {
        const validationResponse = await validateSchema(addLeaveSchema, createUpdateLeaveFormData);
        const {
            name,
            short_name,
            max_leave_per_year,
            carry_forward_limit,
            expire_on,
            leave_unit,
            is_carry_forwardable,
            is_active,
            can_view_on_probation,
            is_credit_on_probation,
            is_encashment,
            is_expirable,
            is_deduction,
            leave_type,
            balance_based_on,
            validity_start_date,
            validity_end_date,
            effective_after_number,
            effective_after_name,
            effective_after_value,
            minimum_days_for_request,
            maximum_days_for_request,
            minimum_interval_between_request,
            applicable_gender,
            applicable_marital_status,
            applicable_employee,
            is_applicable_for_esi_employee,
            minimum_service_days_to_apply,
            encashable_formula,
            max_days_encashable,
        } = createUpdateLeaveFormData
        const applicableGender = setFilterValues(applicable_gender)
        const applicableMaritalStatus = setFilterValues(applicable_marital_status)
        const applicableEmployee = setFilterValues(applicable_employee)
        if (validationResponse.isValid) {
            setErrors({});
            const params = {
                name,
                short_name,
                is_carry_forwardable,
                is_active,
                can_view_on_probation,
                is_credit_on_probation,
                is_encashment,
                is_expirable,
                is_deduction,
                validity_start_date,
                leave_type: leave_type?.value || '',
                validity_end_date,
                encashable_formula,
                max_days_encashable: max_days_encashable || 0,
                effective_after_number: effective_after_number || 0,
                effective_after_name: effective_after_name?.value || '',
                effective_after_value: effective_after_value?.value || '',
                minimum_days_for_request: minimum_days_for_request || 0,
                maximum_days_for_request: maximum_days_for_request || 0,
                minimum_interval_between_request: minimum_interval_between_request || 0,
                applicable_gender: applicableGender || [],
                applicable_marital_status: applicableMaritalStatus || [],
                applicable_employee: applicableEmployee || [],
                balance_based_on: balance_based_on?.value || '',
                expire_on: expire_on?.value || '',
                leave_unit: leave_unit?.value || '',
                max_leave_per_year: max_leave_per_year || 0,
                carry_forward_limit: carry_forward_limit || 0,
                is_applicable_for_esi_employee,
                minimum_service_days_to_apply: minimum_service_days_to_apply || 0
            }
            handleCreateUpdateLeave(params)

        } else {
            setErrors(validationResponse.errors);
        }
    }

    const resetForm = () => {
        setCreateUpdateLeaveFormData({
            name: '',
            short_name: '',
            max_leave_per_year: '',
            carry_forward_limit: '',
            expire_on: null,
            leave_unit: null,
            is_carry_forwardable: false,
            is_active: true,
            can_view_on_probation: true,
            is_credit_on_probation: false,
            is_encashment: false,
            is_expirable: false,
            is_deduction: true,
            leave_type: null,
            balance_based_on: null,
            validity_start_date: '',
            validity_start_date: '',
            validity_end_date: '',
            effective_after_number: '',
            effective_after_name: '',
            effective_after_value: '',
            minimum_days_for_request: '',
            maximum_days_for_request: '',
            minimum_interval_between_request: '',
            applicable_gender: '',
            applicable_marital_status: '',
            applicable_employee: '',
            is_applicable_for_esi_employee: false,
            minimum_service_days_to_apply: '',
            max_days_encashable: '',
            encashable_formula: ''
        })
        setErrors({})
    }

    useEffect(() => {
        return () => {
            resetForm()
        }
    }, [openModel])

    const setFilterValues = (data) => {
        const tempData = []
        data && data?.map((item) => {
            tempData.push(item?.value)
        })
        return tempData
    }

    const handleActiveTab = (type) => {
        setActiveTab(type)
    }

    const toggleCreateFormula = () => {
        setIsOpenFormula(!isOpenFormula)
    }

    return (<>
        <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
            tabIndex={-1}
            style={{
            visibility: openModel ? 'visible' : 'hidden',
                width: "800px"
            }}
            id="interviewDetail"
        >
            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedLeaveData ? t("headings.updateLeaveType") : t("headings.addLeaveType")}</h5>
                <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                    onClick={() => handleOpenClose()}
                />
            </div>

            <div className="offcanvas-body myRequest ">
                <div className="formPanel mrfMain">
                    <ul className="nav nav-tabs leaveTabs mb-4">
                        <li className="nav-item">
                            <button
                                className={`nav-link ${activeTab === "basic" ? "active" : ""}`}
                                data-bs-toggle="tab"
                                data-bs-target="#basic"
                                type="button"
                                onClick={() => handleActiveTab("basic")}
                            >
                                Basic
                            </button>
                        </li>
                        <li className="nav-item">
                            <button
                                className={`nav-link ${activeTab === "rules" ? "active" : ""}`}
                                data-bs-toggle="tab"
                                data-bs-target="#rules"
                                type="button"
                                onClick={() => handleActiveTab("rules")}
                            >
                                Rules
                            </button>
                        </li>
                        <li className="nav-item">
                            <button
                                className={`nav-link ${activeTab === "encashment" ? "active" : ""}`}
                                data-bs-toggle="tab"
                                data-bs-target="#encashment"
                                type="button"
                                onClick={() => handleActiveTab("encashment")}
                            >
                                Encashment
                            </button>
                        </li>
                        <li className="nav-item">
                            <button
                                className={`nav-link ${activeTab === "applicability" ? "active" : ""}`}
                                data-bs-toggle="tab"
                                data-bs-target="#applicability"
                                type="button"
                                onClick={() => handleActiveTab("applicability")}
                            >
                                Applicability
                            </button>
                        </li>

                    </ul>
                    <div className="tab-content">
                        <div className={`tab-pane fade ${activeTab === "basic" ? "show active" : ""}`} id="basic">
                            <div className="row">
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.leaveName")} required={true}
                                        tooltipText={t("tooltips.leaveName")}
                                    />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.leaveName")}
                                        value={createUpdateLeaveFormData?.name}
                                        name="name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.name && (
                                        <CustomError text={errors.name} />
                                    )}
                                </div>
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.abbreviation")} required={true}
                                        tooltipText={t("tooltips.leaveShortName")}
                                    />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.abbreviation")}
                                        value={createUpdateLeaveFormData?.short_name}
                                        name="short_name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.short_name && (
                                        <CustomError text={errors.short_name} />
                                    )}
                                </div>
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.maxLeavePerYear")}
                                        tooltipText={t("tooltips.maxLeavePerYear")}
                                    />
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.leavePerYear")}
                                        value={createUpdateLeaveFormData?.max_leave_per_year}
                                        name="max_leave_per_year"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.leaveUnit")} required={true}
                                        tooltipText={t("tooltips.leaveUnit")}
                                    />
                                    <CustomSelect
                                        data={allLeaveConstantData?.["leaveUnit"]}
                                        placeholder={t("placeholders.selectLeaveUnit")}
                                        value={createUpdateLeaveFormData?.leave_unit || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "leave_unit")}
                                        name="leave_unit"
                                    />
                                    {errors?.leave_unit && (
                                        <CustomError text={errors.leave_unit} />
                                    )}
                                </div>
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.leaveBalanceBasedOn")}
                                        tooltipText={t("tooltips.leaveBalanceBasedOn")}
                                    />
                                    <CustomSelect
                                        data={allLeaveConstantData?.["leaveBalanceBasedOn"]}
                                        placeholder={t("placeholders.selectLeaveBalanceBasedOn")}
                                        value={createUpdateLeaveFormData?.balance_based_on || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "balance_based_on")}
                                        name="balance_based_on"
                                    />
                                </div>
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.leaveType")} required={true}
                                        tooltipText={t("tooltips.leaveType")}
                                    />
                                    <CustomSelect
                                        data={allLeaveConstantData?.["leaveType"]}
                                        placeholder={t("placeholders.selectLeaveType")}
                                        value={createUpdateLeaveFormData?.leave_type || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "leave_type")}
                                        name="leave_type"
                                    />
                                    {errors?.leave_type && (
                                        <CustomError text={errors.leave_type} />
                                    )}
                                </div>
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.validityStartDate")}
                                        tooltipText={t("tooltips.validityStartDate")}
                                    />
                                    <Input
                                        type="date"
                                        placeholder={t("placeholders.validity_start_date")}
                                        value={createUpdateLeaveFormData?.validity_start_date}
                                        name="validity_start_date"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.validityEndDate")}
                                        tooltipText={t("tooltips.validityEndDate")}
                                    />
                                    <Input
                                        type="date"
                                        placeholder={t("placeholders.validity_end_date")}
                                        value={createUpdateLeaveFormData?.validity_end_date}
                                        name="validity_end_date"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>

                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text="Active"
                                                tooltipText={t("tooltips.activeLeave")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"is_active"}
                                                id={`is_active`}
                                                // label={"Active"}
                                                checked={createUpdateLeaveFormData?.is_active}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"is_active"}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.deduction")}
                                                tooltipText={t("tooltips.leaveDeduction")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"is_deduction"}
                                                id={`is_deduction`}
                                                // label={"Deduction"}
                                                checked={createUpdateLeaveFormData?.is_deduction}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"is_deduction"}
                                            />
                                        </div>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.allowedOnNotice")}
                                                tooltipText={t("tooltips.allowedOnNotice")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"is_allowed_on_notice"}
                                                id={`is_allowed_on_notice`}
                                                checked={createUpdateLeaveFormData?.is_allowed_on_notice}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"is_allowed_on_notice"}
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className={`tab-pane fade ${activeTab === "rules" ? "show active" : ""}`} id="rules">
                            <div className="row">
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.minDaysForRequest")}
                                        tooltipText={t("tooltips.minDaysForRequest")}
                                    />
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.minDaysForRequest")}
                                        value={createUpdateLeaveFormData?.minimum_days_for_request}
                                        name="minimum_days_for_request"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.maxDaysForRequest")}
                                        tooltipText={t("tooltips.maxDaysForRequest")}
                                    />
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.maxDaysForRequest")}
                                        value={createUpdateLeaveFormData?.maximum_days_for_request}
                                        name="maximum_days_for_request"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.minIntervalBetweenRequest")}
                                        tooltipText={t("tooltips.minIntervalBetweenRequest")}
                                    />
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.minIntervalBetweenRequest")}
                                        value={createUpdateLeaveFormData?.minimum_interval_between_request}
                                        name="minimum_interval_between_request"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.minimumServiceDaysToApply")}
                                    // tooltipText={t("tooltips.maxLeavePerYear")}
                                    />
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.minimumServiceDaysToApply")}
                                        value={createUpdateLeaveFormData?.minimum_service_days_to_apply}
                                        name="minimum_service_days_to_apply"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>
                                <div className="col-md-12 form-group mb-3 row">
                                    <div className='col-md-2'>
                                        <Label text={t("labels.effectiveAfter")} className={"mt-2"}
                                            tooltipText={t("tooltips.effectiveAfter")} />
                                    </div>
                                    <div className='col-md-2'>
                                        <Input
                                            type="number"
                                            placeholder={t("placeholders.number")}
                                            value={createUpdateLeaveFormData?.effective_after_number}
                                            name="effective_after_number"
                                            onChange={(e) => handleFromChange(e)}
                                        />
                                    </div>
                                    <div className='col-md-3'>
                                        <CustomSelect
                                            data={allLeaveConstantData?.["leaveEffectiveAfterName"]}
                                            placeholder={t("placeholders.effectiveName")}
                                            value={createUpdateLeaveFormData?.effective_after_name || ''}
                                            handleChange={(selectedOption) => handleFromChange(selectedOption, "effective_after_name")}
                                            name="effective_after_name"
                                        />
                                    </div>
                                    <div className='col-md-1' style={{ border: "1px solid lightgray", borderRadius: "4px", backgroundColor: "lightgray", height: "38px" }}>
                                        <Label text={"From"} className={"mt-2"} />
                                    </div>
                                    <div className='col-md-4'>
                                        <CustomSelect
                                            data={allLeaveConstantData?.["leaveEffectiveAfterValue"]}
                                            placeholder={t("placeholders.effectiveValue")}
                                            value={createUpdateLeaveFormData?.effective_after_value || ''}
                                            handleChange={(selectedOption) => handleFromChange(selectedOption, "effective_after_value")}
                                            name="effective_after_value"
                                        />
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.creditOnProbation")}
                                                tooltipText={t("tooltips.creditOnProbation")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"is_credit_on_probation"}
                                                id={`is_credit_on_probation`}
                                                // label={"Credit On Probation"}
                                                checked={createUpdateLeaveFormData?.is_credit_on_probation}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"is_credit_on_probation"}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.viewOnProbation")}
                                                tooltipText={t("tooltips.viewOnProbation")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"can_view_on_probation"}
                                                id={`can_view_on_probation`}
                                                // label={"View on probation"}
                                                checked={createUpdateLeaveFormData?.can_view_on_probation}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"can_view_on_probation"}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.expirable")}
                                                tooltipText={t("tooltips.expirable")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"is_expirable"}
                                                id={`is_expirable`}
                                                checked={createUpdateLeaveFormData?.is_expirable}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"is_expirable"}
                                            />
                                        </div>
                                    </div>
                                </div>
                                {createUpdateLeaveFormData?.is_expirable && <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.expireOn")} />
                                    <CustomSelect
                                        data={allLeaveConstantData?.expiresOn}
                                        placeholder={t("placeholders.selectExpireOn")}
                                        value={createUpdateLeaveFormData?.expire_on || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "expire_on")}
                                        name="expire_on"
                                    />
                                </div>}
                            </div>
                        </div>
                        {/*Encashment Fields start*/}
                        <div className={`tab-pane fade ${activeTab === "encashment" ? "show active" : ""}`} id="encashment">
                            <div className="row">

                                <div className="row ">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.carryForwardable")}
                                                tooltipText={t("tooltips.carryForwardable")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"is_carry_forwardable"}
                                                id={`is_carry_forwardable`}
                                                // label={"Carry Forwardable"}
                                                checked={createUpdateLeaveFormData?.is_carry_forwardable}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"is_carry_forwardable"}
                                            />
                                        </div>
                                    </div>

                                </div>
                                {createUpdateLeaveFormData?.is_carry_forwardable && <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.carryForwardLimit")} />
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.carryForwardLimit")}
                                        value={createUpdateLeaveFormData?.carry_forward_limit}
                                        name="carry_forward_limit"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                </div>}
                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.encashment")}
                                                tooltipText={t("tooltips.encashment")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"is_encashment"}
                                                id={`is_encashment`}
                                                // label={"Encashment"}
                                                checked={createUpdateLeaveFormData?.is_encashment}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"is_encashment"}
                                            />
                                        </div>
                                    </div>
                                </div>

                                {createUpdateLeaveFormData?.is_encashment && <div>
                                    <div className="col-md-6 form-group mb-3">
                                        <Label text={t("labels.maxDaysEncashable")}
                                        />
                                        <Input
                                            type="number"
                                            placeholder={t("placeholders.maxDaysEncashable")}
                                            value={createUpdateLeaveFormData?.max_days_encashable}
                                            name="max_days_encashable"
                                            onChange={(e) => handleFromChange(e)}
                                        />
                                    </div>
                                    <div className="col-md-12 form-group mb-3">
                                        <Label text={t("labels.encashableFormula")}
                                        />
                                        <div className='row'>

                                            <div className="col-md-6">
                                                <Input
                                                    type="text"
                                                    placeholder={t("placeholders.encashableFormula")}
                                                    value={createUpdateLeaveFormData?.encashable_formula}
                                                    name="encashable_formula"
                                                    onChange={(e) => handleFromChange(e)}
                                                />
                                            </div>
                                            <div className="col-md-6">
                                                <CustomButton
                                                    classNames={"btn btn-primary gradientBg ms-1"}
                                                    text={t("btns.createFormula")}
                                                    handleClick={() => toggleCreateFormula()}
                                                />
                                            </div>

                                        </div>
                                    </div>
                                    {<div className="col-md-12 form-group mb-3">

                                        {isOpenFormula && <EncashmentFormula
                                            isOpenFormula={isOpenFormula}
                                            toggleCreateFormula={toggleCreateFormula}
                                            allLeaveConstantData={allLeaveConstantData}
                                            setCreateUpdateLeaveFormData={setCreateUpdateLeaveFormData}
                                        />}
                                    </div>}

                                </div>}
                            </div>
                        </div>
                        {/*Applicability Fields start*/}
                        <div className={`tab-pane fade ${activeTab === "applicability" ? "show active" : ""}`} id="applicability">
                            <div className="row">
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.applicableMaritalStatus")}
                                        tooltipText={t("tooltips.applicableMaritalStatus")}
                                    />
                                    <CustomSelect
                                        data={appConstantData?.maritalStatus}
                                        placeholder={t("placeholders.applicableMaritalStatus")}
                                        value={createUpdateLeaveFormData?.applicable_marital_status || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "applicable_marital_status")}
                                        name="applicable_marital_status"
                                        isMulti={true}
                                    />
                                </div>
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.applicableGender")}
                                        tooltipText={t("tooltips.applicableGender")}
                                    />
                                    <CustomSelect
                                        data={appConstantData?.gender}
                                        placeholder={t("placeholders.applicableGender")}
                                        value={createUpdateLeaveFormData?.applicable_gender || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "applicable_gender")}
                                        name="applicable_gender"
                                        isMulti={true}
                                    />
                                </div>


                                <div className="col-md-12 form-group mb-3">
                                    <Label text={t("labels.applicableEmployee")}
                                        tooltipText={t("tooltips.applicableEmployee")}
                                    />
                                    <CustomSelect
                                        data={allEmployeeList}
                                        placeholder={t("placeholders.applicableEmployee")}
                                        value={createUpdateLeaveFormData?.applicable_employee || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "applicable_employee")}
                                        name="applicable_employee"
                                        isMulti={true}
                                    />
                                </div>
                                <div className="row ">
                                    <div className="col-md-4">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.isApplicableForEsiEmployee")}
                                            // tooltipText={t("tooltips.carryForwardable")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-8">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"is_applicable_for_esi_employee"}
                                                id={`is_applicable_for_esi_employee`}
                                                // label={"Carry Forwardable"}
                                                checked={createUpdateLeaveFormData?.is_applicable_for_esi_employee}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"is_applicable_for_esi_employee"}
                                            />
                                        </div>
                                    </div>

                                </div>

                            </div>
                        </div>
                    </div>

                </div>
            </div>
            <div className="offcanvas-footer">
                <div className="d-flex align-items-center justify-content-end gap-3">
                    <CustomButton
                        text={selectedLeaveData ? t("btns.update") : t("btns.submit")}
                        classNames={"btn btn-org-gradient"}
                        handleClick={() => submitCreateUpdateLeave()}
                        isLoading={selectedLeaveData ? updateLeaveTypePending : addLeaveTypePending}
                    />
                </div>
            </div>
        </div>
        {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdateLeave
