import React, { useEffect, useState } from 'react'
import { CustomButton } from '../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import { commonActions, departmentAction, designationAction, employeeActions, leaveAction } from '../../../core/resource/actions'
import { pagination } from '../../../utils/Constants.config'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import LeaveSettingTable from './LeaveSettingTable'
import CreateUpdateLeave from './CreateUpdateLeave'
import LeaveBanner from "./LeaveBanner"
import LeaveTable from "./LeaveTable"
import CreateUpdateLeaveSettings from './CreateUpdateLeaveSettings'
import { useCustomTranslation } from '../../../translation'
import { isPermission } from '../../../utils/common'

const Leave = () => {
    const dispatch = useDispatch()
    const { t } = useCustomTranslation()
    const leaveTypePer=isPermission("leave_type")
    const leaveSettingPer=isPermission("leave_setting")
    const [openCloseAddLeaveModal, setOpenCloseAddLeave] = useState(false)
    const {
        addLeaveTypePending,
        addLeaveTypeRes,
        addLeaveTypeError,

        getLeaveConstantDataPending,
        getLeaveConstantDataRes,
        getLeaveConstantDataError,

        getAllLeaveTypesPending,
        getAllLeaveTypesRes,
        getAllLeaveTypesError,

        updateLeaveTypePending,
        updateLeaveTypeRes,
        updateLeaveTypeError,

        deleteLeaveTypePending,
        deleteLeaveTypeRes,
        deleteLeaveTypeError,

        getLeaveTypeListPending,
        getLeaveTypeListRes,
        getLeaveTypeListError,

        addLeaveSettingPending,
        addLeaveSettingRes,
        addLeaveSettingError,

        getAllLeaveSettingPending,
        getAllLeaveSettingRes,
        getAllLeaveSettingError,

        updateLeaveSettingPending,
        updateLeaveSettingRes,
        updateLeaveSettingError,

        deleteLeaveSettingPending,
        deleteLeaveSettingRes,
        deleteLeaveSettingError,


    } = useSelector(state => state.leave);
    const {
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    } = useSelector(state => state.department)
    const {
        getDesignationsByDepartmentRes,
        getDesignationsByDepartmentPending,
        getDesignationsByDepartmentError
    } = useSelector(state => state.designation)

    const {
        getAppConstantDataPending,
        getAppConstantDataRes,
        getAppConstantDataError,
    } = useSelector(state => state.common)

    const {
        getAllEmployeePending,
        getAllEmployeeRes,
        getAllEmployeeError,
    } = useSelector(state => state.employee)

    const [allLeaveConstantData, setAllLeaveConstantData] = useState('')
    const [allLeaveTypesData, setLeaveTypesData] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [selectedLeaveData, setSelectedLeaveData] = useState('')
    const [isShowDeleteLeaveTypeModal, setIsShowDeleteLeaveTypeModal] = useState(false)
    const [deleteLeaveTypeId, setDeleteLeaveTypeId] = useState('')
    const [activeTabType, setActiveTabType] = useState('leave')
    const [openCloseLeaveSettingModal, setOpenCloseLeaveSettingModal] = useState(false)
    const [allDepartments, setAllDepartments] = useState('')
    const [allDesignations, setAllDesignations] = useState('')
    const [allLeaveTypeList, setAllLeaveTypeList] = useState('')
    const [allLeaveSettingData, setAllLeaveSettingData] = useState('')
    const [isShowDeleteLeaveSettingModal, setIsShowDeleteLeaveSettingModal] = useState(false)
    const [deleteLeaveSettingId, setDeleteLeaveSettingId] = useState()
    const [selectedLeaveSetting, setSelectedLeaveSetting] = useState('')
    const [leaveSettingId, setLeaveSettingId] = useState('')
    const [appConstantData, setAppConstantData] = useState()
    const [allEmployeeList,setAllEmployeeList]=useState('')


    /* Add Leave Type */
    const toggleAddUpdateLeaveModal = (data) => {
        if (!data) {
            setSelectedLeaveData('')
        } else {
            setSelectedLeaveData(data)
        }
        setOpenCloseAddLeave(!openCloseAddLeaveModal);
    };

    useEffect(() => {
        getLeaveConstantData()
        getAllDepartments()
        handleGetLeaveTypeList()
        getAppConstantData()
        getAllEmployee()
    }, [])

    useEffect(() => {
        handleGetAllSetting()
        getAllLeaveTypes()
    }, [currentPage])

    /* Create Update Leave Type */
    const handleCreateUpdateLeave = (params) => {
        if (selectedLeaveData) {
            params['_id'] = selectedLeaveData?.id || selectedLeaveData?._id
            dispatch(leaveAction.updateLeaveTypeRequest(params));
        }
        else {
            dispatch(leaveAction.addLeaveTypeRequest(params));
        }
    }

    useEffect(() => {
        if (!addLeaveTypePending) {
            if (addLeaveTypeError) {
                dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_ADD_LEAVE_TYPE_STORE"))
                return;
            }
            if (addLeaveTypeRes) {
                if (addLeaveTypeRes?.code === 200) {
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_ADD_LEAVE_TYPE_STORE"))
                    getAllLeaveTypes()
                    setOpenCloseAddLeave(!openCloseAddLeaveModal);
                }
            }
        }
    }, [
        addLeaveTypeRes,
        addLeaveTypeError,
        addLeaveTypePending
    ]);

    useEffect(() => {
        if (!updateLeaveTypePending) {
            if (updateLeaveTypeError) {
                dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_UPDATE_LEAVE_TYPE_STORE"))
                return;
            }
            if (updateLeaveTypeRes) {
                if (updateLeaveTypeRes?.code === 200) {
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_UPDATE_LEAVE_TYPE_STORE"))
                    getAllLeaveTypes()
                    setOpenCloseAddLeave(!openCloseAddLeaveModal);
                }
            }
        }
    }, [
        updateLeaveTypeRes,
        updateLeaveTypeError,
        updateLeaveTypePending
    ]);

    /* Get Leave Constant Data */
    const getLeaveConstantData = (params) => {
        dispatch(leaveAction.getLeaveConstantDataRequest(params));
    }
    useEffect(() => {
        if (!getLeaveConstantDataPending) {
            if (getLeaveConstantDataError) {
                return;
            }
            if (getLeaveConstantDataRes) {
                if (getLeaveConstantDataRes?.code === 200) {
                    const getLeaveConstantData = getLeaveConstantDataRes?.data
                    setAllLeaveConstantData(getLeaveConstantData)
                }
            }
        }
    }, [
        getLeaveConstantDataRes,
        getLeaveConstantDataError,
        getLeaveConstantDataPending
    ]);

    /* Get All Leave Data */
    const getAllLeaveTypes = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(leaveAction.getAllLeaveTypesRequest(params));
    }


    useEffect(() => {
        if (!getAllLeaveTypesPending) {
            if (getAllLeaveTypesError) {
                return;
            }
            if (getAllLeaveTypesRes) {
                if (getAllLeaveTypesRes?.code === 200) {
                    const getAllLeaveTypesResData = getAllLeaveTypesRes?.data
                    setLeaveTypesData(getAllLeaveTypesResData)
                }
            }
        }
    }, [
        getAllLeaveTypesRes,
        getAllLeaveTypesError,
        getAllLeaveTypesPending
    ]);

    /* Delete Leave Type */
    const toggleDeleteLeaveType = (id) => {
        if (id) {
            setDeleteLeaveTypeId(id)
        }
        setIsShowDeleteLeaveTypeModal(!isShowDeleteLeaveTypeModal)
    }

    const deleteLeaveType = (type) => {
        if (type) {
            const params = {
                _id: deleteLeaveTypeId
            }
            dispatch(leaveAction?.deleteLeaveTypeRequest(params))

        } else {
            toggleDeleteLeaveType()
        }
    }

    useEffect(() => {
        if (!deleteLeaveTypePending) {
            if (deleteLeaveTypeError) {
                dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_DELETE_LEAVE_TYPE_STORE"))
                return;
            }
            if (deleteLeaveTypeRes) {
                if (deleteLeaveTypeRes?.code === 200) {
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_DELETE_LEAVE_TYPE_STORE"))
                    if (currentPage > 1 && allLeaveTypesData?.data?.length === 1) {
                        setCurrentPage((pre => pre -= 1))
                    } else {
                        getAllLeaveTypes()
                    }
                    toggleDeleteLeaveType()
                }
            }
        }
    }, [
        deleteLeaveTypeRes,
        deleteLeaveTypeError,
        deleteLeaveTypePending
    ]);

    const handleActiveTab = (type) => {
        setActiveTabType(type)
    }



    /* Get All Departments */
    const getAllDepartments = () => {
        if (!getDepartmentListRes?.data?.length) {
            dispatch(departmentAction.getDepartmentListRequest());
        } else {
            setAllDepartments(getDepartmentListRes?.data)
        }
    }

    useEffect(() => {
        if (!getDepartmentListPending) {
            if (getDepartmentListError) {
                return;
            }
            if (getDepartmentListRes) {
                if (getDepartmentListRes?.code === 200) {
                    const departmentResponse = getDepartmentListRes?.data
                    setAllDepartments(departmentResponse)
                }
            }
        }
    }, [
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    ]);

    /* Department by Designation  */
    const handleDepartmentByDesignation = (id) => {
        const params = {
            department_id: id
        }
        dispatch(designationAction.getDesignationsByDepartmentRequest(params));
    }

    useEffect(() => {
        if (!getDesignationsByDepartmentPending) {
            if (getDesignationsByDepartmentError) {
                return;
            }
            if (getDesignationsByDepartmentRes) {
                if (getDesignationsByDepartmentRes?.code === 200) {
                    const designationResponse = getDesignationsByDepartmentRes?.data || []
                    setAllDesignations(designationResponse)
                }
            }
        }
    }, [
        getDesignationsByDepartmentRes,
        getDesignationsByDepartmentError,
        getDesignationsByDepartmentPending
    ]);


    // GET LEAVE TYPE.... 
    const handleGetLeaveTypeList = () => {
        dispatch(leaveAction.getLeaveTypeListRequest());
    }

    useEffect(() => {
        if (!getLeaveTypeListPending) {
            if (getLeaveTypeListError) {
                return;
            }
            if (getLeaveTypeListRes) {
                if (getLeaveTypeListRes?.code === 200) {
                    const getLeaveTypeListResData = getLeaveTypeListRes?.data
                    setAllLeaveTypeList(getLeaveTypeListResData)
                }
            }
        }
    }, [
        getLeaveTypeListRes,
        getLeaveTypeListError,
        getLeaveTypeListPending
    ]);


    /* Add Update Setting Type */
    const toggleAddUpdateLeaveSettingModal = (selectedData) => {
        if (selectedData) {
            setSelectedLeaveSetting(selectedData)
            setLeaveSettingId(selectedData?._id)
        } else {
            setSelectedLeaveSetting('')
        }
        setOpenCloseLeaveSettingModal(!openCloseLeaveSettingModal);
    };

    const handleAddUpdateLeaveSetting = (payload) => {
        if (selectedLeaveSetting) {
            payload["_id"] = leaveSettingId || ''
            dispatch(leaveAction.updateLeaveSettingRequest(payload));
        } else {
            dispatch(leaveAction.addLeaveSettingRequest(payload));
        }
    }

    useEffect(() => {
        if (!addLeaveSettingPending) {
            if (addLeaveSettingError) {
                dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_ADD_LEAVE_SETTING_STORE"))
                return;
            }
            if (addLeaveSettingRes) {
                if (addLeaveSettingRes?.code === 200) {
                    setOpenCloseLeaveSettingModal(!openCloseLeaveSettingModal);
                    handleGetAllSetting()
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_ADD_LEAVE_SETTING_STORE"))

                }
            }
        }
    }, [
        addLeaveSettingRes,
        addLeaveSettingError,
        addLeaveSettingPending
    ]);

    useEffect(() => {
        if (!updateLeaveSettingPending) {
            if (updateLeaveSettingError) {
                dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_UPDATE_LEAVE_SETTING_STORE"))
                return;
            }
            if (updateLeaveSettingRes) {
                if (updateLeaveSettingRes?.code === 200) {
                    setOpenCloseLeaveSettingModal(!openCloseLeaveSettingModal);
                    handleGetAllSetting()
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_UPDATE_LEAVE_SETTING_STORE"))

                }
            }
        }
    }, [
        updateLeaveSettingRes,
        updateLeaveSettingError,
        updateLeaveSettingPending
    ]);

    /* Get Leave Settings */
    const handleGetAllSetting = () => {
        const params = {
            page: currentPage,
            limit: pagination?.limit,
        }
        dispatch(leaveAction.getAllLeaveSettingRequest(params));
    }

    useEffect(() => {
        if (!getAllLeaveSettingPending) {
            if (getAllLeaveSettingError) {
                return;
            }
            if (getAllLeaveSettingRes) {
                if (getAllLeaveSettingRes?.code === 200) {
                    setAllLeaveSettingData(getAllLeaveSettingRes?.data)
                }
            }
        }
    }, [
        getAllLeaveSettingRes,
        getAllLeaveSettingError,
        getAllLeaveSettingPending
    ]);

    /* Delete Leave Settings */
    const toggleDeleteSettingTypeModal = (id) => {
        if (id) {
            setDeleteLeaveSettingId(id)
        }
        setIsShowDeleteLeaveSettingModal(!isShowDeleteLeaveSettingModal)
    }

    const handleDeleteSettingType = (type) => {
        if (type) {
            const params = {
                _id: deleteLeaveSettingId
            }
            dispatch(leaveAction?.deleteLeaveSettingRequest(params))

        } else {
            toggleDeleteSettingTypeModal()
        }

    }

    useEffect(() => {
        if (!deleteLeaveSettingPending) {
            if (deleteLeaveSettingError) {
                dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_DELETE_LEAVE_SETTING_STORE"))
                return;
            }
            if (deleteLeaveSettingRes) {
                if (deleteLeaveSettingRes?.code === 200) {
                    toggleDeleteSettingTypeModal()
                    if (currentPage > 1 && allLeaveSettingData?.data?.length === 1) {
                        setCurrentPage((pre => pre -= 1))
                    } else {
                        handleGetAllSetting()
                    }
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_DELETE_LEAVE_SETTING_STORE"))

                }
            }
        }
    }, [
        deleteLeaveSettingRes,
        deleteLeaveSettingError,
        deleteLeaveSettingPending
    ]);


   
    /* Get All Employee List*/
    const getAllEmployee = () => {
        dispatch(employeeActions.getAllEmployeeRequest())
    }
    useEffect(() => {
        if (!getAllEmployeePending) {
            if (getAllEmployeeError) {
                return;
            }
            if (getAllEmployeeRes) {
                if (getAllEmployeeRes?.code === 200) {

                    setAllEmployeeList(getAllEmployeeRes?.data)
                }
            }
        }
    }, [
        getAllEmployeeRes,
        getAllEmployeeError,
        getAllEmployeePending
    ]);


     /*Get App Constant Data */
    const getAppConstantData = () => {
        dispatch(commonActions.getAppConstantDataRequest())
    }
    useEffect(() => {
        if (!getAppConstantDataPending) {
            if (getAppConstantDataError) {
                return;
            }
            if (getAppConstantDataRes) {
                if (getAppConstantDataRes?.code === 200) {
                    setAppConstantData(getAppConstantDataRes?.data)
                }
            }
        }
    }, [
        getAppConstantDataRes,
        getAppConstantDataError,
        getAppConstantDataPending
    ]);


    return (
        <>
           {(leaveSettingPer|| leaveTypePer)&& <LeaveBanner handleActiveTab={handleActiveTab} />}
            {leaveTypePer&&activeTabType === "leave" && <>
                <div className="d-flex  justify-content-end ">
                    <CustomButton
                        classNames={"btn btn-primary gradientBg mt-3"}
                        text={t("btns.addLeaveType")}
                        handleClick={() => toggleAddUpdateLeaveModal()}
                        slug={"add_leave_type"}
                    />
                </div>
                <LeaveTable
                    allLeaveTypesData={allLeaveTypesData}
                    setSelectedLeaveData={setSelectedLeaveData}
                    toggleAddUpdateLeaveModal={toggleAddUpdateLeaveModal}
                    toggleDeleteLeaveType={toggleDeleteLeaveType}
                    setCurrentPage={setCurrentPage}
                    isLoading={getAllLeaveTypesPending}
                    currentPage={currentPage}
                />
                <CreateUpdateLeave
                    openModel={openCloseAddLeaveModal}
                    handleOpenClose={toggleAddUpdateLeaveModal}
                    handleCreateUpdateLeave={handleCreateUpdateLeave}
                    allLeaveConstantData={allLeaveConstantData}
                    selectedLeaveData={selectedLeaveData}
                    allLeaveTypeList={allLeaveTypeList}
                    appConstantData={appConstantData}
                    allEmployeeList={allEmployeeList}
                />
                {isShowDeleteLeaveTypeModal && (
                    <ApproveRejectModal
                        onApproveReject={deleteLeaveType}
                        isShow={isShowDeleteLeaveTypeModal}
                        desc={t("alerts.deletePopup.desc").replace('$1',"Leave Type")}
                        btnRightLoading={deleteLeaveTypePending}
                        btnTextRight={t("btns.delete")}
                        btnTextLeft={t("btns.cancel")}

                    />)
                }
            </>}
                {console.log(leaveSettingPer,activeTabType,"leaveSettingPer&&activeTabType")}
            {leaveSettingPer&&activeTabType === "leave_setting" && <>
                <div className="d-flex  justify-content-end ">
                    <CustomButton
                        classNames={"btn btn-primary gradientBg mt-3"}
                        text={t("btns.addLeaveSetting")}
                        handleClick={() => toggleAddUpdateLeaveSettingModal()}
                        slug={"add_leave_setting"}
                    />
                </div>

                <CreateUpdateLeaveSettings
                    openModel={openCloseLeaveSettingModal}
                    handleOpenClose={toggleAddUpdateLeaveSettingModal}
                    allDepartments={allDepartments}
                    handleDepartmentByDesignation={handleDepartmentByDesignation}
                    allDesignations={allDesignations}
                    allLeaveConstantData={allLeaveConstantData}
                    allLeaveTypeList={allLeaveTypeList}
                    handleAddUpdateLeaveSetting={handleAddUpdateLeaveSetting}
                    selectedLeaveSetting={selectedLeaveSetting}
                />

                <LeaveSettingTable
                    currentPage={currentPage}
                    allLeaveSettingData={allLeaveSettingData}
                    handleOpenClose={toggleAddUpdateLeaveSettingModal}
                    toggleDeleteSettingTypeModal={toggleDeleteSettingTypeModal}
                    setCurrentPage={setCurrentPage}

                />
                {isShowDeleteLeaveSettingModal && (
                    <ApproveRejectModal
                        onApproveReject={handleDeleteSettingType}
                        isShow={isShowDeleteLeaveSettingModal}
                        desc={t("alerts.deletePopup.desc").replace('$1',"Leave Setting")}
                        btnRightLoading={deleteLeaveSettingPending}
                        btnTextRight={t("btns.delete")}
                        btnTextLeft={t("btns.cancel")}
                    />)
                }

            </>}

        </>
    )
}

export default Leave
