import React, { useEffect, useMemo, useState } from 'react'
import { useSelector } from 'react-redux'
import { validateSchema } from '../../../utils/ValidationHelper'
import { addLeaveSettingSchema } from '../../../utils/ValidationSchema'
import CustomSelect from '../../commanComponent/CustomSelect'
import { CustomButton, CustomError } from '../../globalComponents'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import Image from '../../commanComponent/Image'
import { useCustomTranslation } from '../../../translation'
import { RuleRenderer } from './RuleRenderer'
import { defaultMultiSelectOptions, getDataWithLabelValue } from '../../../utils/common'


const CreateUpdateLeaveSettings = (props) => {
    const { t } = useCustomTranslation()
    const { handleOpenClose, openModel,
        allLeaveConstantData,
        selectedLeaveData,
        allDepartments,
        handleDepartmentByDesignation,
        allDesignations,
        allLeaveTypeList,
        handleAddUpdateLeaveSetting,
        selectedLeaveSetting
    } = props
    const [errors, setErrors] = useState('')
    const { addLeaveSettingPending, updateLeaveSettingPending } = useSelector(state => state.leave)
    const [activeTab, setActiveTab] = useState('basic')
    const [leaveSettingFormData, setLeaveSettingFormData] = useState({
        leave_type: null,
        department: null,
        designation: null,
        consider_present_status: [],
        base_on_credit: '',
        is_active: true,
        count_weekend_as_leave: false,
        count_holiday_as_leave: false,
        allow_request_for: null,
        allow_request_days: '',
        allow_request_value: '',
        administration_management: false,
        enable_file_option: false,
        leave_exceed_for_file: '',
        count_leave_for_preceding_weekends: false,
        count_leave_for_succeeding_weekends: false,
        count_leave_for_preceding_holiday: false,
        count_leave_for_succeeding_holiday: false,
        not_allowed_clubbing_with: ''
    })
    const [leaveRules, setLeaveRules] = useState([{
        operator: '',
        working_days: '',
        leave_credit: ''

    }])
    const [firstMonthRules, setFirstMonthRules] = useState([{
        from: '',
        to: '',
        leave_credit: ''

    }])
    const [lastMonthRules, setLastMonthRules] = useState([{
        from: '',
        to: '',
        leave_credit: ''

    }])
    const handleFromChange = (e, fieldName) => {
        // For custom selects, use the value passed directly as `e`
        const name = fieldName || e?.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };

        if (fieldName === "department") {
            handleDepartmentByDesignation(value?.value)
            setLeaveSettingFormData(prevState => ({
                ...prevState,
                ["designation"]: null,
            }));
        }

        setLeaveSettingFormData(prevState => ({
            ...prevState,
            [name]: type === 'checkbox' ? checked : value,
        }));

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };


    const handleChangeLeaveRule = (e, fieldName, index) => {
        const name = fieldName || e.target?.name;
        const { value } = e?.target ? e?.target : { value: e };

        setLeaveRules((prevState) =>
            prevState?.map((rule, idx) =>
                idx === index
                    ? { ...rule, [name]: value } // Update specific rule at index
                    : rule
            )
        );
    }

    const deleteLeaveRule = (index) => {
        const updateLeaveRule = leaveRules.filter((_, i) => i !== index);
        setLeaveRules(updateLeaveRule);
    };

    const addLeaveRule = () => {
        setLeaveRules((prevState) => [
            ...prevState,
            {
                operator: '',
                working_days: '',
                leave_credit: '',
            },
        ]);
    };

    const renderLeaveRules = useMemo(() => {
        return (
            leaveRules && leaveRules?.map((rule, index) => <div className='row' key={index}>
                <div className="col-md-3 form-group mt-2 mrfViewOnly">
                    <Label text={t("labels.presentStatus")}
                    />
                </div>
                <div className="col-md-3 form-group mb-3">
                    <CustomSelect
                        data={allLeaveConstantData?.leaveOperators}
                        placeholder={t("placeholders.selectOperation")}
                        value={rule?.operator}
                        handleChange={(selectedOption) => handleChangeLeaveRule(selectedOption, "operator", index)}
                        name="operator"
                    />
                </div>
                <div className="col-md-3 form-group mb-3">
                    <Input
                        type="number"
                        placeholder={t("placeholders.workingDays")}
                        value={rule?.working_days}
                        name="working_days"
                        onChange={(e) => handleChangeLeaveRule(e, null, index)}
                    />
                </div>
                <div className="col-md-3 form-group mb-3">
                    <Input
                        type="number"
                        placeholder={t("placeholders.leaveCredit")}
                        value={rule?.leave_credit}
                        name="leave_credit"
                        onChange={(e) => handleChangeLeaveRule(e, null, index)}
                    />
                </div>
                <div className="d-flex justify-content-end mb-3">
                    {leaveRules.length - 1 == index &&
                        <div className='me-2' onClick={addLeaveRule} >
                            <Image imgSrc={"images/add-icon3.svg"} altText={""} width={32}></Image>
                        </div>
                    }
                    {leaveRules.length > 1 && <div onClick={() => deleteLeaveRule(index)}>
                        <Image imgSrc={"images/deletebg-Icon.svg"} altText={""} width={32}></Image>
                    </div>}
                </div>
            </div>)
        )
    }, [leaveRules, allLeaveConstantData])

    const submitCreateUpdateLeaveSetting = async () => {
        const {
            leave_type,
            department,
            designation,
            consider_present_status,
            base_on_credit,
            is_active,
            count_weekend_as_leave,
            count_holiday_as_leave,
            allow_request_for,
            allow_request_days,
            allow_request_value,
            administration_management,
            enable_file_option,
            leave_exceed_for_file,
            count_leave_for_preceding_weekends,
            count_leave_for_succeeding_weekends,
            count_leave_for_preceding_holiday,
            count_leave_for_succeeding_holiday,
            not_allowed_clubbing_with,

        } = leaveSettingFormData
        const validationResponse = await validateSchema(addLeaveSettingSchema, leaveSettingFormData);
        if (validationResponse.isValid) {
            const presentStatus = getValuesPresentStatus(consider_present_status)
            const updateLeaveRules = handleLeaveRules(leaveRules)
            const updateFirstMonthRule = handleLeaveMonthRules(firstMonthRules)
            const updateLastMonthRule = handleLeaveMonthRules(lastMonthRules)
            const notAllowedClubbingWith = setFilterValues(not_allowed_clubbing_with)
            const payload = {
                leave_type_id: leave_type?.value,
                department_id: department?.value,
                designation_id: designation?.value,
                leave_rules: updateLeaveRules,
                consider_present_status: presentStatus,
                base_on_credit: base_on_credit?.value,
                is_active: is_active,
                count_weekend_as_leave: count_weekend_as_leave || 0,
                count_holiday_as_leave: count_holiday_as_leave || 0,
                allow_request_for: allow_request_for?.value,
                allow_request_days: allow_request_days || 0,
                allow_request_value,
                administration_management,
                enable_file_option,
                leave_exceed_for_file: leave_exceed_for_file || 0,
                first_month_rules: updateFirstMonthRule,
                last_month_rules: updateLastMonthRule,
                count_leave_for_preceding_weekends,
                count_leave_for_succeeding_weekends,
                count_leave_for_preceding_holiday,
                count_leave_for_succeeding_holiday,
                not_allowed_clubbing_with: notAllowedClubbingWith || [],
            }

            setErrors({});
            handleAddUpdateLeaveSetting(payload)
        } else {
            setErrors(validationResponse.errors);

        }
    }

    const setFilterValues = (data) => {
        const tempData = []
        data && data?.map((item) => {
            tempData.push(item?.value)
        })
        return tempData
    }

    const getValuesPresentStatus = (data) => {
        let tempPresentStatus = []
        data && data?.length > 1 && data?.map((item) => {
            tempPresentStatus.push(item?.value)
        })
        return tempPresentStatus
    }
    const handleLeaveRules = (data) => {
        return data && data?.map((item, index) => ({
            ...item,
            operator: item?.operator?.value,
        }))
    }
    const handleLeaveMonthRules = (data) => {
        return data && data?.map((item, index) => ({
            ...item,
            from: item?.from?.value,
            to: item?.to?.value,
        }))
    }

    useEffect(() => {
        if (selectedLeaveSetting) {
            const tempConsiderPresentStatus = getDataWithLabelValue(selectedLeaveSetting?.consider_present_status)
            const notAllowedClubbingWith =defaultMultiSelectOptions(allLeaveTypeList,selectedLeaveSetting.not_allowed_clubbing_with)
            setLeaveSettingFormData({
                leave_type: { value: selectedLeaveSetting?.leave_type_id, label: selectedLeaveSetting?.leave_type_name },
                department: { value: selectedLeaveSetting?.department_id, label: selectedLeaveSetting?.department_name },
                designation: { value: selectedLeaveSetting?.designation_id, label: selectedLeaveSetting?.designation_name },
                consider_present_status: tempConsiderPresentStatus,
                base_on_credit: { value: selectedLeaveSetting?.base_on_credit, label: selectedLeaveSetting?.base_on_credit_name },
                is_active: selectedLeaveSetting?.is_active,
                count_weekend_as_leave: selectedLeaveSetting?.count_weekend_as_leave || '',
                count_holiday_as_leave: selectedLeaveSetting?.count_holiday_as_leave || '',
                allow_request_for: { value: selectedLeaveSetting?.allow_request_for, label: selectedLeaveSetting?.allow_request_for_name },
                allow_request_days: selectedLeaveSetting?.allow_request_days || '',
                allow_request_value: selectedLeaveSetting?.allow_request_value,
                administration_management: selectedLeaveSetting?.administration_management,
                enable_file_option: selectedLeaveSetting?.enable_file_option,
                leave_exceed_for_file: selectedLeaveSetting?.leave_exceed_for_file || '',
                count_leave_for_preceding_weekends: selectedLeaveSetting?.count_leave_for_preceding_weekends,
                count_leave_for_succeeding_weekends: selectedLeaveSetting?.count_leave_for_succeeding_weekends,
                count_leave_for_preceding_holiday: selectedLeaveSetting?.count_leave_for_preceding_holiday,
                count_leave_for_succeeding_holiday: selectedLeaveSetting?.count_leave_for_succeeding_holiday,
                not_allowed_clubbing_with: notAllowedClubbingWith,
            })
            setLeaveRules((prev) =>
                selectedLeaveSetting?.leave_rules?.map((item) => ({
                    ...item,
                    operator: { value: item?.operator, label: item?.operator }
                }))
            );
            if (selectedLeaveSetting?.first_month_rules) {
                setFirstMonthRules((prev) =>
                    selectedLeaveSetting?.first_month_rules?.map((item) => ({
                        ...item,
                        from: { value: item?.from, label: item?.from },
                        to: { value: item?.to, label: item?.to }
                    })))
            }
            if (selectedLeaveSetting?.last_month_rules) {
                setLastMonthRules((prev) =>
                    selectedLeaveSetting?.last_month_rules?.map((item) => ({
                        ...item,
                        from: { value: item?.from, label: item?.from },
                        to: { value: item?.to, label: item?.to }
                    })))
            }

        }
    }, [selectedLeaveSetting])

    const resetForm = () => {
        setLeaveSettingFormData({
            leave_type: null,
            department: null,
            designation: null,
            consider_present_status: [],
            base_on_credit: '',
            is_active: true,
            count_weekend_as_leave: false,
            count_holiday_as_leave: false,
            allow_request_for: null,
            allow_request_days: '',
            allow_request_value: '',
            administration_management: false,
            enable_file_option: false,
            leave_exceed_for_file: '',
            count_leave_for_preceding_weekends: false,
            count_leave_for_succeeding_weekends: false,
            count_leave_for_preceding_holiday: false,
            count_leave_for_succeeding_holiday: false,
            not_allowed_clubbing_with: ''
        })
        setLeaveRules([{
            operator: '',
            working_days: '',
            leave_credit: ''
        }])
        setLastMonthRules([{
            from: '',
            to: '',
            leave_credit: ''

        }])
        setFirstMonthRules([{
            from: '',
            to: '',
            leave_credit: ''
        }])
        setErrors({})
    }

    useEffect(() => {
        return () => {
            resetForm()
        }
    }, [openModel])

    const handleChangeLeaveMonthRule = (e, field, index, setRules, rules) => {
        const newRules = [...rules];
        newRules[index][field || e.target.name] = field ? e : e.target.value;
        setRules(newRules); // Update the state with the modified rules
    };

    const addLeaveMonthRule = (setRules, rules) => {
        setRules([...rules, { from: '', to: '', leave_credit: '' }]);
    };

    const deleteLeaveMonthRule = (index, setRules, rules) => {
        const updatedRules = rules.filter((_, i) => i !== index);
        setRules(updatedRules);
    };

    const handleActiveTab = (type) => {
        setActiveTab(type)
    }

    return (
        <>
        <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
            tabIndex={-1}
            style={{
                visibility: openModel ? 'visible' : 'hidden',
                width: "800px"
            }}
            id="interviewDetail"
        >
            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedLeaveSetting ? "Update" : "Add"} Leave Setting</h5>
                <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                    onClick={() => handleOpenClose()}
                />
            </div>

            <div className="offcanvas-body myRequest ">
                <div className="formPanel mrfMain">
                    <ul className="nav nav-tabs leaveTabs mb-4">
                        <li className="nav-item">
                            <button
                                className={`nav-link ${activeTab === "basic" ? "active" : ""}`}
                                data-bs-toggle="tab"
                                data-bs-target="#basic"
                                type="button"
                                onClick={() => handleActiveTab("basic")}
                            >
                                Basic
                            </button>
                        </li>
                        <li className="nav-item">
                            <button
                                className={`nav-link ${activeTab === "credit" ? "active" : ""}`}
                                data-bs-toggle="tab"
                                data-bs-target="#credit"
                                type="button"
                                onClick={() => handleActiveTab("credit")}
                            >
                                Credit
                            </button>
                        </li>
                        <li className="nav-item">
                            <button
                                className={`nav-link ${activeTab === "sandwich" ? "active" : ""}`}
                                data-bs-toggle="tab"
                                data-bs-target="#sandwich"
                                type="button"
                                onClick={() => handleActiveTab("sandwich")}
                            >
                                Sandwich
                            </button>
                        </li>
                    </ul>
                    <div className="tab-content">

                        {/* Basic Fields Start*/}
                        <div className={`tab-pane fade ${activeTab === "basic" ? "show active" : ""}`} id="basic">
                            <div className="row">
                                <div className="row">
                                    <div className="col-md-6 form-group mb-3">
                                        <Label text={t("labels.department")} required={true}
                                            tooltipText={t("tooltips.leaveSettingDepartment")}
                                        />
                                        <CustomSelect
                                            data={allDepartments}
                                            placeholder={t("placeholders.selectDepartment")}
                                            value={leaveSettingFormData?.department || ''}
                                            handleChange={(selectedOption) => {
                                                handleFromChange(selectedOption, "department");

                                            }
                                            }
                                            name="department"
                                        />

                                        {errors?.department && (
                                            <CustomError text={errors.department} />
                                        )}
                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <Label text={t("labels.designation")} required={true}
                                            tooltipText={t("tooltips.leaveSettingDesignation")}
                                        />
                                        <CustomSelect
                                            data={allDesignations}
                                            placeholder={t("placeholders.selectDesignation")}
                                            value={leaveSettingFormData?.designation || ''}
                                            handleChange={(selectedOption) => handleFromChange(selectedOption, "designation")}
                                            name="designation"
                                        />

                                        {errors?.designation && (
                                            <CustomError text={errors.designation} />
                                        )}
                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <Label text={t("labels.leaveType")} required={true}
                                            tooltipText={t("tooltips.leaveSettingType")}
                                        />
                                        <CustomSelect
                                            data={allLeaveTypeList}
                                            placeholder={t("placeholders.selectLeaveType")}
                                            value={leaveSettingFormData?.leave_type || ''}
                                            handleChange={(selectedOption) => handleFromChange(selectedOption, "leave_type")}
                                            name="leave_type"
                                        />
                                        {errors?.leave_type && (
                                            <CustomError text={errors.leave_type} />
                                        )}

                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <Label text={t("labels.notAllowedClubbingWith")}
                                            tooltipText={t("tooltips.notAllowedClubbingWith")}
                                        />
                                        <CustomSelect
                                            data={allLeaveTypeList}
                                            placeholder={t("placeholders.notAllowedClubbingWith")}
                                            value={leaveSettingFormData?.not_allowed_clubbing_with || ''}
                                            handleChange={(selectedOption) => handleFromChange(selectedOption, "not_allowed_clubbing_with")}
                                            name="not_allowed_clubbing_with"
                                            isMulti={true}
                                        />
                                    </div>

                                    <div className="col-md-6 form-group mb-3">
                                        <Label text={t("labels.allowRequestFor")}
                                            tooltipText={t("tooltips.allowRequestFor")}
                                        />
                                        <CustomSelect
                                            data={allLeaveConstantData?.leaveAllowRequestsFor}
                                            placeholder={t("placeholders.allowRequestFor")}
                                            value={leaveSettingFormData?.allow_request_for || ''}
                                            handleChange={(selectedOption) => handleFromChange(selectedOption, "allow_request_for")}
                                            name="allow_request_for"
                                        />
                                    </div>
                                    {leaveSettingFormData?.allow_request_for && <div className="col-md-6 form-group mb-3">
                                        <Label text={t("labels.allowRequestDays")}
                                            tooltipText={t("tooltips.allowRequestDays")}
                                        />
                                        <Input
                                            type="number"
                                            placeholder={t("placeholders.allowRequestDays")}
                                            value={leaveSettingFormData?.allow_request_days}
                                            name="allow_request_days"
                                            onChange={(e) => handleFromChange(e)}
                                        />
                                    </div>}

                                    <div className="row">
                                        <div className="col-md-4">
                                            <div className="form-group mb-4">
                                                <Label text={t("labels.isActive")}
                                                    tooltipText={t("tooltips.LeaveSettingIsActive")}
                                                />
                                            </div>
                                        </div>
                                        <div className="col-md-8">
                                            <div className="form-group mb-3">
                                                <CustCheckbox
                                                    key={"is_active"}
                                                    id={`is_active`}
                                                    // label={"Active"}
                                                    checked={leaveSettingFormData?.is_active}
                                                    handleChange={(e) => handleFromChange(e)}
                                                    name={"is_active"}
                                                />
                                            </div>
                                        </div>
                                    </div>



                                </div>
                            </div>
                        </div>
                        {/* Credit Fields Start*/}
                        <div className={`tab-pane fade ${activeTab === "credit" ? "show active" : ""}`} id="credit">
                            <div className="row">
                                <div className="col-md-6 form-group mb-3">
                                    <Label text={t("labels.baseOnCredit")}
                                        tooltipText={t("tooltips.baseOnCredit")}
                                    />
                                    <CustomSelect
                                        data={allLeaveConstantData?.leaveCreditBasis}
                                        placeholder={t("placeholders.selectBaseOnCredit")}
                                        value={leaveSettingFormData?.base_on_credit || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "base_on_credit")}
                                        name="base_on_credit"
                                    />
                                </div>
                                <div className="col-md-12 form-group mb-3">
                                    <Label text={t("labels.considerPresentStatus")}
                                        tooltipText={t("tooltips.considerPresentStatus")}
                                    />
                                    <CustomSelect
                                        data={allLeaveConstantData?.presentStatus}
                                        placeholder={t("placeholders.selectConsiderPresentStatus")}
                                        value={leaveSettingFormData?.consider_present_status || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "consider_present_status")}
                                        name="consider_present_status"
                                        isMulti={true}
                                    />
                                </div>

                                <div className="col-md-12 form-group mb-3 row">
                                    <Label text={t("labels.leaveRule")}
                                        tooltipText={t("tooltips.leaveRule")}
                                    />
                                    {renderLeaveRules}
                                </div>
                                <div className="col-md-12 form-group mb-3 row">
                                    <Label text={t("labels.firstMonthRules")}
                                        tooltipText={t("tooltips.firstMonthRules")}
                                    />

                                    <RuleRenderer
                                        rules={firstMonthRules}
                                        addRule={() => addLeaveMonthRule(setFirstMonthRules, firstMonthRules)}
                                        deleteRule={(index) => deleteLeaveMonthRule(index, setFirstMonthRules, firstMonthRules)}
                                        handleChange={(e, field, index) => handleChangeLeaveMonthRule(e, field, index, setFirstMonthRules, firstMonthRules)}
                                        allLeaveConstantData={allLeaveConstantData}
                                    />
                                </div>
                                <div className="col-md-12 form-group mb-3 row">
                                    <Label text={t("labels.lastMonthRules")}
                                        tooltipText={t("tooltips.lastMonthLeaveRule")}
                                    />
                                    <RuleRenderer
                                        rules={lastMonthRules}
                                        addRule={() => addLeaveMonthRule(setLastMonthRules, lastMonthRules)}
                                        deleteRule={(index) => deleteLeaveMonthRule(index, setLastMonthRules, lastMonthRules)}
                                        handleChange={(e, field, index) => handleChangeLeaveMonthRule(e, field, index, setLastMonthRules, lastMonthRules)}
                                        allLeaveConstantData={allLeaveConstantData}
                                    />
                                </div>
                            </div>
                        </div>
                        {/* Sandwich fields Start*/}
                        <div className={`tab-pane fade ${activeTab === "sandwich" ? "show active" : ""}`} id="sandwich">
                            <div className="row">
                                <div className="row">
                                    <div className="col-md-4">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.countWeekendLeave")}
                                                tooltipText={t("tooltips.countWeekendAsLeave")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-8">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"count_weekend_as_leave"}
                                                id={`count_weekend_as_leave`}
                                                // label={"Active"}
                                                checked={leaveSettingFormData?.count_weekend_as_leave}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"count_weekend_as_leave"}
                                            />
                                        </div>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="col-md-4">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.countHolidayLeave")}
                                                tooltipText={t("tooltips.countHolidayAsLeave")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-8">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"count_holiday_as_leave"}
                                                id={`count_holiday_as_leave`}
                                                // label={"Active"}
                                                checked={leaveSettingFormData?.count_holiday_as_leave}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"count_holiday_as_leave"}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-4">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.countLeaveForPrecedingWeekends")}
                                            // tooltipText={t("tooltips.countWeekendAsLeave")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-8">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"count_leave_for_preceding_weekends"}
                                                id={`count_leave_for_preceding_weekends`}
                                                checked={leaveSettingFormData?.count_leave_for_preceding_weekends}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"count_leave_for_preceding_weekends"}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-4">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.countLeaveForSucceedingWeekends")}
                                            // tooltipText={t("tooltips.countWeekendAsLeave")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-8">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"count_leave_for_succeeding_weekends"}
                                                id={`count_leave_for_succeeding_weekends`}
                                                checked={leaveSettingFormData?.count_leave_for_succeeding_weekends}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"count_leave_for_succeeding_weekends"}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-4">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.countLeaveForPrecedingHoliday")}
                                            // tooltipText={t("tooltips.countWeekendAsLeave")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-8">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"count_leave_for_preceding_holiday"}
                                                id={`count_leave_for_preceding_holiday`}
                                                checked={leaveSettingFormData?.count_leave_for_preceding_holiday}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"count_leave_for_preceding_holiday"}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-4">
                                        <div className="form-group mb-3">
                                            <Label text={t("labels.countLeaveForSucceedingHoliday")}
                                            // tooltipText={t("tooltips.countWeekendAsLeave")}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-8">
                                        <div className="form-group mb-3">
                                            <CustCheckbox
                                                key={"count_leave_for_succeeding_holiday"}
                                                id={`count_leave_for_succeeding_holiday`}
                                                checked={leaveSettingFormData?.count_leave_for_succeeding_holiday}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"count_leave_for_succeeding_holiday"}
                                            />
                                        </div>
                                    </div>
                                </div>


                            </div>
                        </div>
                    </div>


                </div>
            </div>
            <div className="offcanvas-footer">
                <div className="d-flex align-items-center justify-content-end gap-3">
                    <CustomButton
                        text={selectedLeaveSetting ? t("btns.update") : t("btns.submit")}
                        classNames={"btn btn-org-gradient"}
                        handleClick={() => submitCreateUpdateLeaveSetting()}
                        isLoading={selectedLeaveData ? updateLeaveSettingPending : addLeaveSettingPending}
                    />
                </div>
            </div>
        </div>
         {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdateLeaveSettings
