import React, { useEffect, useMemo, useState } from 'react'
import { getBadgeColor, isPermission } from '../../../utils/common'
import moment from 'moment'
import { CustomButton } from '../../globalComponents'
import { CustomTable } from '../../reuseableComponents'
import { useCustomTranslation } from '../../../translation'
import { Icon } from '../../commanComponent'

const LeaveTable = (props) => {
  const { t } = useCustomTranslation()
  const {
    allLeaveTypesData,
    isLoading,
    toggleAddUpdateLeaveModal,
    toggleDeleteLeaveType,
    currentPage,
    setCurrentPage,
  } = props
  const updateLeaveTypePer = isPermission("update_leave_type")
  const deleteLeaveTypePer = isPermission("delete_leave_type")


  const handleUpdateLeave = (data) => {
    toggleAddUpdateLeaveModal(data)
  }


  const scopedSlots = useMemo(() => ({
    sn: (_, index) => (
      <span>{index + 1}</span>
    ),
    is_active: item => (
      <span
        className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
      >
        {item.is_active ? "Active" : "Inactive"}
      </span>
    ),

    created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
    action: item => (
      <div className="btn-group actionDrp">
        <button type="button" className="btn dropdown-toggle-split"
          data-bs-toggle="dropdown" aria-expanded="false">
          <i className="bi bi-three-dots-vertical"></i>
        </button>
        <ul className="dropdown-menu">
          {updateLeaveTypePer&&<li>
            <span className="dropdown-item"
              onClick={() => handleUpdateLeave(item)}
            >
              <Icon className={"bi bi-pencil-square me-1"} />Update</span>
          </li>}
         {deleteLeaveTypePer&&<li>
            <span className="dropdown-item"
              onClick={() => toggleDeleteLeaveType(item.id || item._id)}
            >
              <Icon className={"bi bi-trash me-1"} />Delete</span>
          </li>}
        </ul>
      </div>

    )
  }), [allLeaveTypesData]);


  const fields = [
    { key: 'sn', label: 'S.N' },
    { key: 'name', label: 'Name' },
    { key: 'short_name', label: 'Short Name' },
    { key: 'expire_on', label: 'Expire On' },
    { key: 'leave_unit', label: 'Leave Unit' },
    { key: 'is_active', label: 'Status' },
    { key: 'created_at', label: 'Created At' },
    ((updateLeaveTypePer||deleteLeaveTypePer) &&{ key: 'action', label: 'Action' })
  ];
  return (
    <>
      <CustomTable
        data={allLeaveTypesData?.data}
        fields={fields}
        isLoading={isLoading}
        pagination={true}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        totalPages={allLeaveTypesData?.totalPages}
        tableName={t("headings.leave")}
        scopedSlots={scopedSlots}
      />

    </>
  )
}

export default LeaveTable
