import { Input, Label } from "../../commanComponent";
import CustomSelect from "../../commanComponent/CustomSelect";
import Image from "../../commanComponent/Image";
import { useCustomTranslation } from "../../../translation";
export const RuleRenderer = ({ rules, addRule, deleteRule, handleChange, allLeaveConstantData }) => {
    const { t } = useCustomTranslation()
    return (
        <>
            {rules && rules?.map((rule, index) => (
                <div className="row" key={index}>
                    <div className="col-md-3 form-group mt-2 mrfViewOnly">
                        <Label text={t("labels.date")} />
                    </div>
                    <div className="col-md-3 form-group mb-3">
                        <CustomSelect
                            data={allLeaveConstantData?.dateConstants}
                            placeholder={t("placeholders.from")}
                            value={rule?.from}
                            handleChange={(selectedOption) => handleChange(selectedOption, "from", index)}
                            name="from"
                        />
                    </div>
                    <div className="col-md-3 form-group mb-3">
                        <CustomSelect
                            data={allLeaveConstantData?.dateConstants}
                            placeholder={t("placeholders.to")}
                            value={rule?.to}
                            handleChange={(selectedOption) => handleChange(selectedOption, "to", index)}
                            name="to"
                        />

                    </div>
                    <div className="col-md-3 form-group mb-3">
                        <Input
                            type="number"
                            placeholder={t("placeholders.leaveCredit")}
                            value={rule?.leave_credit}
                            name="leave_credit"
                            onChange={(e) => handleChange(e, null, index)}
                        />
                    </div>
                    <div className="d-flex justify-content-end mb-3">
                        {rules.length - 1 === index && (
                            <div className="me-2" onClick={addRule}>
                                <Image imgSrc={"images/add-icon3.svg"} altText={""} width={32} />
                            </div>
                        )}
                        {rules.length > 1 && (
                            <div onClick={() => deleteRule(index)}>
                                <Image imgSrc={"images/deletebg-Icon.svg"} altText={""} width={32} />
                            </div>
                        )}
                    </div>
                </div>
            ))}
        </>
    );
};
