import React, { useMemo } from 'react'
import { useSelector } from 'react-redux'
import { CustomTable } from '../../reuseableComponents'
import { getBadgeColor, isPermission } from '../../../utils/common'
import moment from 'moment'
import { CustomButton } from '../../globalComponents'
import { Icon } from '../../commanComponent'

const LeaveSettingTable = (props) => {
  const {
    allLeaveSettingData,
    setSelectedLeaveData,
    handleAddUpdateLeaveModal,
    toggleDeleteSettingTypeModal,
    currentPage,
    setCurrentPage,
    handleOpenClose
  } = props
  const updateLeaveSettingPer = isPermission("update_leave_setting")
  const deleteLeaveSettingPer = isPermission("delete_leave_setting")
  const { getAllLeaveSettingPending } = useSelector(state => state.leave)
  const fields = [
    { key: 'sn', label: 'S.N' },
    { key: 'leave_type_name', label: 'Leave Name' },
    { key: 'department_name', label: 'Department Name' },
    { key: 'designation_name', label: 'Designation Name' },
    { key: 'base_on_credit_name', label: 'Base On Credit Name' },
    { key: 'is_allowed_on_notice', label: 'Allowed On Notice' },
    { key: 'is_active', label: 'Status' },
    // { key: 'created_at', label: 'Created At' },
   ((updateLeaveSettingPer||deleteLeaveSettingPer)&& { key: 'action', label: 'Action' })
  ];

  const scopedSlots = useMemo(() => ({
    sn: (_, index) => (
      <span>{index + 1}</span>
    ),
    is_active: item => (
      <span
        className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
      >
        {item.is_active ? "Active" : "Inactive"}
      </span>
    ),
    is_allowed_on_notice: item => (
      <span
        className={`badge ${getBadgeColor(item.is_allowed_on_notice ? "Active" : "Inactive")}`}
      >
        {item.is_allowed_on_notice ? "Yes" : "No"}
      </span>
    ),

    created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
    action: item => (
      <div className="btn-group actionDrp">
        <button type="button" className="btn dropdown-toggle-split"
          data-bs-toggle="dropdown" aria-expanded="false">
          <i className="bi bi-three-dots-vertical"></i>
        </button>
        <ul className="dropdown-menu">
          {updateLeaveSettingPer &&<li>
            <span className="dropdown-item"
              onClick={() => handleOpenClose(item)}
            >
              <Icon className={"bi bi-pencil-square me-1"} />Update</span>
          </li>}
           {deleteLeaveSettingPer &&<li>
            <span className="dropdown-item"
              onClick={() => toggleDeleteSettingTypeModal(item._id)}
            >
              <Icon className={"bi bi-trash me-1"} />Delete</span>
          </li>}
        </ul>
      </div>
    )
  }), [allLeaveSettingData]);

  return (
    <>
      <CustomTable
        data={allLeaveSettingData?.data}
        fields={fields}
        isLoading={getAllLeaveSettingPending}
        pagination={true}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        totalPages={allLeaveSettingData?.totalPages}
        tableName={"Leave Setting"}
        // onSearch={handleSearch}
        scopedSlots={scopedSlots}
      />
    </>
  )
}

export default LeaveSettingTable
